\n\n\n// Pagination\n\n.pagination {\n\n  li {\n    display: inline-block;\n    border-radius: 2px;\n    text-align: center;\n    vertical-align: top;\n    height: 30px;\n\n    a {\n      color: #444;\n      display: inline-block;\n      font-size: 1.2rem;\n      padding: 0 10px;\n      line-height: 30px;\n    }\n\n    &.active a { color: #fff; }\n\n    &.active { background-color: $primary-color; }\n\n    &.disabled a {\n      cursor: default;\n      color: #999;\n    }\n\n    i {\n      font-size: 2rem;\n    }\n  }\n\n\n  li.pages ul li {\n    display: inline-block;\n    float: none;\n  }\n}\n@media #{$medium-and-down} {\n  .pagination {\n    width: 100%;\n\n    li.prev,\n    li.next {\n      width: 10%;\n    }\n\n    li.pages {\n      width: 80%;\n      overflow: hidden;\n      white-space: nowrap;\n    }\n  }\n}\n\n// Breadcrumbs\n.breadcrumb {\n  font-size: 18px;\n  color: rgba(255,255,255, .7);\n\n  i,\n  [class^=\"mdi-\"], [class*=\"mdi-\"],\n  i.material-icons {\n    display: inline-block;\n    float: left;\n    font-size: 24px;\n  }\n\n  &:before {\n    content: '\\E5CC';\n    color: rgba(255,255,255, .7);\n    vertical-align: top;\n    display: inline-block;\n    font-family: 'Material Icons';\n    font-weight: normal;\n    font-style: normal;\n    font-size: 25px;\n    margin: 0 10px 0 8px;\n    -webkit-font-smoothing: antialiased;\n  }\n\n  &:first-child:before {\n    display: none;\n  }\n\n  &:last-child {\n    color: #fff;\n  }\n}\n\n// Parallax\n.parallax-container {\n  position: relative;\n  overflow: hidden;\n  height: 500px;\n\n  .parallax {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    z-index: -1;\n\n    img {\n      opacity: 0;\n      position: absolute;\n      left: 50%;\n      bottom: 0;\n      min-width: 100%;\n      min-height: 100%;\n      transform: translate3d(0,0,0);\n      transform: translateX(-50%);\n    }\n  }\n}\n\n// Pushpin\n.pin-top, .pin-bottom {\n  position: relative;\n}\n.pinned {\n  position: fixed !important;\n}\n\n/*********************\n  Transition Classes\n**********************/\n\nul.staggered-list li {\n  opacity: 0;\n}\n\n.fade-in {\n  opacity: 0;\n  transform-origin: 0 50%;\n}\n\n\n/*********************\n  Media Query Classes\n**********************/\n.hide-on-small-only, .hide-on-small-and-down {\n  @media #{$small-and-down} {\n    display: none !important;\n  }\n}\n.hide-on-med-and-down {\n  @media #{$medium-and-down} {\n    display: none !important;\n  }\n}\n.hide-on-med-and-up {\n  @media #{$medium-and-up} {\n    display: none !important;\n  }\n}\n.hide-on-med-only {\n  @media only screen and (min-width: $small-screen) and (max-width: $medium-screen) {\n    display: none !important;\n  }\n}\n.hide-on-large-only {\n  @media #{$large-and-up} {\n    display: none !important;\n  }\n}\n.hide-on-extra-large-only {\n  @media #{$extra-large-and-up} {\n    display: none !important;\n  }\n}\n.show-on-extra-large {\n  @media #{$extra-large-and-up} {\n    display: block !important;\n  }\n}\n.show-on-large {\n  @media #{$large-and-up} {\n    display: block !important;\n  }\n}\n.show-on-medium {\n  @media only screen and (min-width: $small-screen) and (max-width: $medium-screen) {\n    display: block !important;\n  }\n}\n.show-on-small {\n  @media #{$small-and-down} {\n    display: block !important;\n  }\n}\n.show-on-medium-and-up {\n  @media #{$medium-and-up} {\n    display: block !important;\n  }\n}\n.show-on-medium-and-down {\n  @media #{$medium-and-down} {\n    display: block !important;\n  }\n}\n\n\n// Center text on mobile\n.center-on-small-only {\n  @media #{$small-and-down} {\n    text-align: center;\n  }\n}\n\n// Footer\n.page-footer {\n  padding-top: 20px;\n  color: $footer-font-color;\n  background-color: $footer-bg-color;\n\n  .footer-copyright {\n    overflow: hidden;\n    min-height: 50px;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 10px 0px;\n    color: $footer-copyright-font-color;\n    background-color: $footer-copyright-bg-color;\n  }\n}\n\n// Tables\ntable, th, td {\n   border: none;\n}\n\ntable {\n  width:100%;\n  display: table;\n  border-collapse: collapse;\n  border-spacing: 0;\n\n  &.striped {\n    tr {\n      border-bottom: none;\n    }\n\n    > tbody {\n      > tr:nth-child(odd) {\n        background-color: $table-striped-color;\n      }\n\n      > tr > td {\n        border-radius: 0;\n      }\n    }\n  }\n\n  &.highlight > tbody > tr {\n    transition: background-color .25s ease;\n    &:hover {\n      background-color: $table-striped-color;\n    }\n  }\n\n  &.centered {\n    thead tr th, tbody tr td {\n      text-align: center;\n    }\n  }\n}\n\ntr {\n  border-bottom: 1px solid $table-border-color;\n}\n\ntd, th{\n  padding: 15px 5px;\n  display: table-cell;\n  text-align: left;\n  vertical-align: middle;\n  border-radius: 2px;\n}\n\n// Responsive Table\n@media #{$medium-and-down} {\n\n  table.responsive-table {\n    width: 100%;\n    border-collapse: collapse;\n    border-spacing: 0;\n    display: block;\n    position: relative;\n\n    td:empty:before {\n      content: '\\00a0';\n    }\n\n    th,\n    td {\n      margin: 0;\n      vertical-align: top;\n    }\n\n    th { text-align: left; }\n    thead {\n      display: block;\n      float: left;\n\n      tr {\n        display: block;\n        padding: 0 10px 0 0;\n\n        th::before {\n          content: \"\\00a0\";\n        }\n      }\n    }\n    tbody {\n      display: block;\n      width: auto;\n      position: relative;\n      overflow-x: auto;\n      white-space: nowrap;\n\n      tr {\n        display: inline-block;\n        vertical-align: top;\n      }\n    }\n    th {\n      display: block;\n      text-align: right;\n    }\n    td {\n      display: block;\n      min-height: 1.25em;\n      text-align: left;\n    }\n    tr {\n      border-bottom: none;\n      padding: 0 10px;\n    }\n\n    /* sort out borders */\n    thead {\n      border: 0;\n      border-right: 1px solid $table-border-color;\n    }\n  }\n\n}\n\n\n// Collections\n.collection {\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  border: 1px solid $collection-border-color;\n  border-radius: 2px;\n  overflow: hidden;\n  position: relative;\n\n  .collection-item {\n    background-color: $collection-bg-color;\n    line-height: $collection-line-height;\n    padding: 10px 20px;\n    margin: 0;\n    border-bottom: 1px solid $collection-border-color;\n\n    // Avatar Collection\n    &.avatar {\n      min-height: 84px;\n      padding-left: 72px;\n      position: relative;\n\n      // Don't style circles inside preloader classes.\n      &:not(.circle-clipper) > .circle,\n      :not(.circle-clipper) > .circle {\n        position: absolute;\n        width: 42px;\n        height: 42px;\n        overflow: hidden;\n        left: 15px;\n        display: inline-block;\n        vertical-align: middle;\n      }\n      i.circle {\n        font-size: 18px;\n        line-height: 42px;\n        color: #fff;\n        background-color: #999;\n        text-align: center;\n      }\n\n\n      .title {\n        font-size: 16px;\n      }\n\n      p {\n        margin: 0;\n      }\n\n      .secondary-content {\n        position: absolute;\n        top: 16px;\n        right: 16px;\n      }\n\n    }\n\n\n    &:last-child {\n      border-bottom: none;\n    }\n\n    &.active {\n      background-color: $collection-active-bg-color;\n      color: $collection-active-color;\n\n      .secondary-content {\n        color: #fff;\n      }\n    }\n  }\n  a.collection-item{\n    display: block;\n    transition: .25s;\n    color: $collection-link-color;\n    &:not(.active) {\n      &:hover {\n        background-color: $collection-hover-bg-color;\n      }\n    }\n  }\n\n  &.with-header {\n    .collection-header {\n      background-color: $collection-bg-color;\n      border-bottom: 1px solid $collection-border-color;\n      padding: 10px 20px;\n    }\n    .collection-item {\n      padding-left: 30px;\n    }\n    .collection-item.avatar {\n      padding-left: 72px;\n    }\n  }\n\n}\n// Made less specific to allow easier overriding\n.secondary-content {\n  float: right;\n  color: $secondary-color;\n}\n.collapsible .collection {\n  margin: 0;\n  border: none;\n}\n\n\n\n// Responsive Videos\n.video-container {\n    position: relative;\n    padding-bottom: 56.25%;\n    height: 0;\n    overflow: hidden;\n\n    iframe, object, embed {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n    }\n}\n\n// Progress Bar\n.progress {\n    position: relative;\n    height: 4px;\n    display: block;\n    width: 100%;\n    background-color: lighten($progress-bar-color, 40%);\n    border-radius: 2px;\n    margin: $element-top-margin 0 $element-bottom-margin 0;\n    overflow: hidden;\n  .determinate {\n    position: absolute;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    background-color: $progress-bar-color;\n    transition: width .3s linear;\n  }\n  .indeterminate {\n    background-color: $progress-bar-color;\n    &:before {\n      content: '';\n      position: absolute;\n      background-color: inherit;\n      top: 0;\n      left:0;\n      bottom: 0;\n      will-change: left, right;\n      // Custom bezier\n      animation: indeterminate 2.1s cubic-bezier(0.650, 0.815, 0.735, 0.395) infinite;\n\n    }\n    &:after {\n      content: '';\n      position: absolute;\n      background-color: inherit;\n      top: 0;\n      left:0;\n      bottom: 0;\n      will-change: left, right;\n      // Custom bezier\n      animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.840, 0.440, 1.000) infinite;\n      animation-delay: 1.15s;\n    }\n  }\n}\n@keyframes indeterminate {\n    0% {\n      left: -35%;\n      right:100%;\n    }\n    60% {\n      left: 100%;\n      right: -90%;\n    }\n    100% {\n      left: 100%;\n      right: -90%;\n    }\n}\n\n@keyframes indeterminate-short {\n    0% {\n      left: -200%;\n      right: 100%;\n    }\n    60% {\n      left: 107%;\n      right: -8%;\n    }\n    100% {\n      left: 107%;\n      right: -8%;\n    }\n}\n\n\n/*******************\n  Utility Classes\n*******************/\n\n.hide {\n  display: none !important;\n}\n\n// Text Align\n.left-align {\n  text-align: left;\n}\n.right-align {\n  text-align: right\n}\n.center, .center-align {\n  text-align: center;\n}\n\n.left {\n  float: left !important;\n}\n.right {\n  float: right !important;\n}\n\n// No Text Select\n.no-select {\n  user-select: none;\n}\n\n.circle {\n  border-radius: 50%;\n}\n\n.center-block {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.truncate {\n  display: block;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.no-padding {\n  padding: 0 !important;\n}\n",".container {\n  margin: 0 auto;\n  max-width: 1280px;\n  width: 90%;\n}\n@media #{$medium-and-up} {\n  .container {\n    width: 85%;\n  }\n}\n@media #{$large-and-up} {\n  .container {\n    width: 70%;\n  }\n}\n.col .row {\n  margin-left: (-1 * $gutter-width / 2);\n  margin-right: (-1 * $gutter-width / 2);\n}\n\n.section {\n  padding-top: 1rem;\n  padding-bottom: 1rem;\n\n  &.no-pad {\n    padding: 0;\n  }\n  &.no-pad-bot {\n    padding-bottom: 0;\n  }\n  &.no-pad-top {\n    padding-top: 0;\n  }\n}\n\n\n// Mixins to eliminate code repitition\n@mixin reset-offset {\n  margin-left: auto;\n  left: auto;\n  right: auto;\n}\n@mixin grid-classes($size, $i, $perc) {\n  &.offset-#{$size}#{$i} {\n    margin-left: $perc;\n  }\n  &.pull-#{$size}#{$i} {\n    right: $perc;\n  }\n  &.push-#{$size}#{$i} {\n    left: $perc;\n  }\n}\n\n\n.row {\n  margin-left: auto;\n  margin-right: auto;\n  margin-bottom: 20px;\n\n  // Clear floating children\n  &:after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n\n  .col {\n    float: left;\n    box-sizing: border-box;\n    padding: 0 $gutter-width / 2;\n    min-height: 1px;\n\n    &[class*=\"push-\"],\n    &[class*=\"pull-\"] {\n      position: relative;\n    }\n\n    $i: 1;\n    @while $i <= $num-cols {\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n      &.s#{$i} {\n        width: $perc;\n        @include reset-offset;\n      }\n      $i: $i + 1;\n    }\n\n    $i: 1;\n    @while $i <= $num-cols {\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n      @include grid-classes(\"s\", $i, $perc);\n      $i: $i + 1;\n    }\n\n    @media #{$medium-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        &.m#{$i} {\n          width: $perc;\n          @include reset-offset;\n        }\n        $i: $i + 1\n      }\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        @include grid-classes(\"m\", $i, $perc);\n        $i: $i + 1;\n      }\n    }\n\n    @media #{$large-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        &.l#{$i} {\n          width: $perc;\n          @include reset-offset;\n        }\n        $i: $i + 1;\n      }\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        @include grid-classes(\"l\", $i, $perc);\n        $i: $i + 1;\n      }\n    }\n\n    @media #{$extra-large-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        &.xl#{$i} {\n          width: $perc;\n          @include reset-offset;\n        }\n        $i: $i + 1;\n      }\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        @include grid-classes(\"xl\", $i, $perc);\n        $i: $i + 1;\n      }\n    }\n  }\n}\n","nav {\n  &.nav-extended {\n    height: auto;\n\n    .nav-wrapper {\n      min-height: $navbar-height-mobile;\n      height: auto;\n    }\n\n    .nav-content {\n      position: relative;\n      line-height: normal;\n    }