or-teal-dark;\n\n@each $c in $colors {\n  $i: index($colors, $c);\n\n  .background-#{$c} {\n    background: nth($creg, $i);\n    color: $color-white;\n    position: relative;\n\n    & > .row {\n      position: relative;\n      display: flex;\n      z-index: 2;\n\n      @media #{$small-and-down} {\n        flex-direction: column;\n      }\n    }\n\n    h1, h2, h3 {\n      color: $color-white;\n    }\n\n    &.background-point-left {\n      & > .row > .col:nth-child(1)::before {\n        @include triangle('bottom right', nth($cdark, $i));\n      }\n\n      & > .row > .col:nth-child(1)::after {\n        @media #{$small-and-down} {\n          @include triangle('bottom left', nth($cdark, $i));\n        }\n        @media #{$medium-and-up} {\n          @include triangle('top right', nth($cdark, $i));\n        }\n      }\n\n      & > .row > .col:nth-child(2) {\n        background: nth($cdark, $i);\n      }\n    }\n\n    &.background-point-right {\n      & > .row > .col:nth-child(2)::before {\n        @media #{$small-and-down} {\n          @include triangle('top right', nth($cdark, $i));\n        }\n        @media #{$medium-and-up} {\n          @include triangle('bottom left', nth($cdark, $i));\n        }\n      }\n\n      & > .row > .col:nth-child(2)::after {\n        @include triangle('top left', nth($cdark, $i));\n      }\n\n      & > .row > .col:nth-child(1) {\n        background: nth($cdark, $i);\n      }\n    }\n\n    &.background-order-swap {\n      & > .row > .col.flex-order-m1 {\n        background: transparent;\n\n        @media #{$small-and-down} {\n          &::before, &::after {\n            display: none;\n          }\n        }\n        @media #{$medium-and-up} {\n          order: 1;\n\n          &::before {\n            @include triangle('bottom right', nth($cdark, $i));\n          }\n\n          &::after {\n            @include triangle('top right', nth($cdark, $i));\n          }\n        }\n      }\n\n      & > .row > .col.flex-order-m2 {\n        background: nth($cdark, $i);\n\n        @media #{$small-and-down} {\n          &::before {\n            @include triangle('bottom left', nth($creg, $i));\n          }\n\n          &::after {\n            @include triangle('bottom right', nth($creg, $i));\n          }\n        }\n        @media #{$medium-and-up} {\n          order: 2;\n\n          &::before, &::after {\n            display: none;\n          }\n        }\n      }\n    }\n\n    html[data-contrast=\"true\"] & {\n      background: $color-black;\n      border-bottom: 1px solid $color-white;\n      border-top: 1px solid $color-white;\n\n      &.background-point-right, &.background-point-left, &.background-order-swap {\n        & > .row > .col.col::before, & > .row > .col.col::after {\n          display: none;\n        }\n\n        & > .row > .col.col {\n          background: $color-black;\n        }\n      }\n    }\n  }\n}\n",".btn.btn {\n  border: 4px solid $color-purple-dark;\n  color: $color-white;\n  font-family: $font-serif;\n  font-weight: normal;\n  text-transform: none;\n  line-height: 2.15rem;\n  min-height: rem-calc(50px);\n  height: auto;\n  text-decoration: none;\n\n  &:hover, &:focus, &:active {\n    background: lighten($color-purple, 10%);\n    color: $color-white;\n\n    @extend .z-depth-4;\n  }\n\n  &.disabled {\n    background: $color-gray-light;\n    border-color: $color-gray-medium;\n    color: $color-gray-medium !important;\n    pointer-events: all;\n  }\n}\n\n.btn-lg.btn {\n  display: block;\n  padding: 1rem;\n  height: auto;\n}\n\n.btn-wide {\n  padding-left: 3em;\n  padding-right: 3em;\n}\n\n.btn-teal.btn-teal {\n  background: $color-teal;\n  border-color: $color-teal-dark;\n\n  &:hover, &:focus, &:active {\n    background: lighten($color-teal, 10%);\n  }\n}\n\n.btn-green.btn-green {\n  background: $color-green;\n  border-color: $color-green-dark;\n\n  &:hover, &:focus, &:active {\n    background: lighten($color-green, 10%);\n  }\n}\n",".facetwp-facet {\n  margin-bottom: 2rem;\n}\n\n.facetwp-search-wrap {\n  display: block;\n\n  .facetwp-btn {\n    line-height: 3rem;\n    opacity: 1;\n    top: 15px;\n    right: 15px;\n\n    &::before {\n      background-image: url('../images/icon-search.svg');\n\n      html[data-contrast=\"true\"] & {\n        background-image: url('../images/icon-search-white.svg');\n      }\n    }\n  }\n}\n\n.facet-label {\n  @extend h3;\n\n  font-family: $font-serif;\n  font-weight: bold;\n  margin: 0.75em 0;\n}\n\n.facetwp-checkbox.facetwp-checkbox {\n  background-image: url('../images/unchecked.png');\n  background-position: 0 0.4em;\n  line-height: 1.25em;\n  padding-bottom: 0.5em;\n  margin-bottom: 0;\n\n  &.checked {\n    background-image: url('../images/checked.png');\n\n    html[data-contrast=\"true\"] & {\n      background-image: url('../images/checked-white.png');\n    }\n  }\n}\n\n.facetwp-overlay.facetwp-overlay {\n  background-color: rgba($color-green, 0.7);\n\n  html[data-contrast=\"true\"] & {\n    background-color: rgba($color-black, 0.7);\n  }\n}\n\n.facetwp-counts {\n  padding: 1em 0;\n  color: $color-gray-medium;\n  font-size: 0.8em;\n  width: 100%;\n}\n","/** Search form */\n.search-form {\n  height: auto;\n  clear: both;\n  line-height: 2.2rem;\n  position: relative;\n  margin: 0;\n  display: flex;\n  align-items: flex-end;\n\n  &::after {\n    content: '';\n    position: absolute;\n    bottom: 10px;\n    right: 10px;\n    width: 28px;\n    height: 28px;\n    background-repeat: no-repeat;\n    background-size: contain;\n    background-position: center;\n    background-image: url('../images/icon-search.svg');\n    pointer-events: none;\n\n    html[data-contrast=\"true\"] & {\n      background-image: url('../images/icon-search-white.svg');\n    }\n  }\n\n  label {\n    float: left;\n    width: calc(100% - 50px);\n  }\n\n  input.search-field {\n    height: 50px;\n    margin: 0;\n    padding: 0 10px;\n    border: 0 !important;\n    border-radius: 0;\n    box-sizing: border-box;\n    background: $color-gray-light;\n\n    &::placeholder {\n      color: $color-gray-dark;\n    }\n\n    html[data-contrast=\"true\"] & {\n      color: $color-white;\n      background: $color-gray-dark;\n      border: 1px solid $color-white;\n    }\n  }\n\n  input.search-submit {\n    display: block;\n    height: 50px;\n    width: 50px;\n    color: $color-gray-light;\n    text-indent: -100em;\n    overflow: hidden;\n    border: none;\n    background: $color-gray-light;\n    cursor: pointer;\n\n    html[data-contrast=\"true\"] & {\n      background: $color-black;\n      color: $color-white;\n      border: 1px solid $color-white;\n    }\n  }\n}\n\nselect {\n  display: block;\n  border: 1px solid lighten($color-gray-medium, 20%);\n  border-radius: 0;\n\n  &.default, option[value=\"\"] {\n    color: $color-gray-medium;\n  }\n}\n\nlabel {\n  color: $color-black;\n  font-size: 1rem;\n}\n\ninput[type=\"text\"], input[type=\"number\"], input[type=\"tel\"], input[type=\"email\"], input[type=\"date\"] {\n  &.facetwp-search, &.wpcf7-form-control {\n    background: $color-white;\n    border: 1px solid lighten($color-gray-medium, 20%);\n    font-size: 1rem;\n    margin: 0;\n    padding: 0 30px 0 5px;\n    box-sizing: border-box;\n\n    &::placeholder {\n      color: $color-gray-medium;\n    }\n\n    .home & {\n      border-radius: 5px;\n    }\n  }\n}\n\ninput[type=password][name=\"post_password\"] {\n  border: 1px solid lighten($color-gray-medium, 20%);\n}\n\nlabel[for*=\"pwbox\"] {\n  & + input[type=\"submit\"] {\n    @extend .btn;\n  }\n}\n\ninput[type=\"radio\"], input[type=\"checkbox\"] {\n  &:focus + .wpcf7-list-item-label {\n    outline: 1px dotted $color-gray-medium;\n  }\n}\n\n.wpcf7-not-valid, input.error {\n  border: 2px solid $error-color !important;\n  background-color: lighten($error-color, 60%) !important;\n}\n\n.wpcf7-not-valid-tip, .wpcf7-validation-errors {\n  color: $error-color;\n}\n\n.wpcf7-list-item {\n  display: block;\n  margin-bottom: 0.8em;\n\n  .wpcf7-list-item-label {\n    height: auto !important;\n  }\n}\n\n.form-step {\n  position: relative;\n\n  &.hidden {\n    display: none;\n  }\n}\n\n.form-section {\n  position: relative;\n  margin-bottom: 20px;\n  border: 1px solid $color-gray-dark;\n\n  & > legend {\n    font-family: $font-serif;\n    font-weight: bold;\n    font-size: 1.3em;\n    margin-bottom: 0.5em;\n  }\n\n  &.hidden {\n    display: none;\n  }\n}\n\n.form-row {\n  position: relative;\n  margin: 0 0 rem-calc(20px);\n  // border: 1px solid $navy;\n  // background: white;\n  // padding: 20px;\n}\n\n// AJAX loading spinner\n@keyframes spinner {\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n.loading-spinner {\n  position: relative;\n  display: inline-block;\n  vertical-align: text-top;\n  margin: 0 30px;\n\n  &::before {\n    content: '';\n    box-sizing: border-box;\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    width: 40px;\n    height: 40px;\n    margin-top: -10px;\n    margin-left: -10px;\n    border-radius: 50%;\n    border: 10px solid $color-green;\n    border-top-color: $color-green-dark;\n    animation: spinner 0.6s linear infinite;\n  }\n}\n\n// Form Progress\nol.form-progress {\n  list-style: none;\n  display: table;\n  margin: 1.5rem 0;\n  table-layout: fixed;\n  width: 100%;\n  padding: 0;\n  counter-reset: progressStep;\n\n  &:focus {\n    outline: 0;\n\n    li[data-step-current] {\n      outline: $color-gray-dark dotted 1px;\n    }\n  }\n\n  @media #{$small-and-down} {\n    padding: 0;\n  }\n\n  li {\n    text-align: center;\n    display: table-cell;\n    list-style: none;\n    line-height: 1.2em;\n    margin: 0;\n    padding: 2rem 0 0;\n    position: relative;\n    width: 100%;\n    counter-increment: progressStep;\n\n    &::before, &::after {\n      background: $color-teal;\n      color: $color-white;\n      display: block;\n      position: absolute;\n    }\n\n    &::before {\n      content: counter(progressStep);\n      border-radius: 3px;\n      font-weight: bold;\n      height: 1.5rem;\n      left: 50%;\n      margin-left: -0.75rem;\n      line-height: 1.5rem;\n      padding-left: 0.1rem;\n      top: 0;\n      transform: scale(1, 1);\n      width: 1.5rem;\n      z-index: 1;\n    }\n\n    &::after {\n      content: '';\n      height: 0.25rem;\n      left: -50%;\n      top: 0.65rem;\n      width: 100%;\n    }\n\n    &[data-step-current] {\n      color: $color-purple-dark;\n      font-weight: bold;\n\n      &::before, &::after {\n        background: $color-purple-dark;\n        color: $color-white;\n      }\n    }\n\n    &[data-step-complete] {\n      cursor: pointer;\n\n      &::before, &::after {\n        background: $color-purple;\n        color: $color-white;\n      }\n    }\n\n    &:first-child::after {\n      display: none;\n    }\n  }\n}\n\n.vr-signup-form {\n  max-width: 300px;\n\n  @media #{$small-and-down} {\n    margin: 0 auto 60px;\n  }\n\n  label {\n    font-weight: bold;\n  }\n\n  input[type=\"text\"], input[type=\"email\"] {\n    background: $color-white;\n    border: 1px solid lighten($color-gray-medium, 20%);\n    font-size: 1rem;\n    margin: 10px 0 20px;\n    padding: 0 30px 0 5px;\n    box-sizing: border-box;\n    width: 100%;\n\n    html[data-contrast=\"true\"] & {\n      background: $color-black;\n      border: 1px solid $color-white;\n      color: $color-white;\n    }\n  }\n\n  input[type=\"submit\"] {\n    @extend .btn;\n\n    width: 100%;\n  }\n}\n",".social-icons {\n  text-align: right;\n  line-height: 1em;\n  padding: 10px 0;\n\n  li {\n    padding: 0 0 0 6px;\n    display: inline-block;\n    float: none;\n\n    a {\n      display: block;\n      position: relative;\n      height: 50px;\n      width: 50px;\n      padding: 0;\n      overflow: hidden;\n      text-align: left;\n      text-indent: -100em;\n      background: $color-teal;\n      border: 2px solid $color-teal;\n\n      &::before {\n        content: '';\n        position: absolute;\n        top: 11px;\n        left: 11px;\n        width: 26px;\n        height: 26px;\n        background-repeat: no-repeat;\n        background-size: contain;\n        background-position: center;\n        transition: all 0.3s;\n      }\n\n      &:hover, &:active, &:focus {\n        background: lighten($color-teal, 10%);\n        border-color: $color-teal;\n\n        @extend .z-depth-2;\n      }\n    }\n\n    &.icon-facebook a::before {\n      background-image: url('../images/icon-facebook.svg');\n    }\n\n    &.icon-linkedin a::before {\n      background-image: url('../images/icon-linkedin.svg');\n    }\n\n    &.icon-twitter a::before {\n      background-image: url('../images/icon-twitter.svg');\n    }\n\n    &.icon-youtube a::before {\n      background-image: url('../images/icon-youtube.svg');\n    }\n\n    html[data-contrast=\"true\"] & {\n      &.icon-facebook a {\n        &:hover, &:focus {\n          &::before {\n            background-image: url('../images/icon-facebook-black.svg');\n          }\n        }\n      }\n\n      &.icon-linkedin a {\n        &:hover, &:focus {\n          &::before {\n            background-image: url('../images/icon-linkedin-black.svg');\n          }\n        }\n      }\n\n      &.icon-twitter a {\n        &:hover, &:focus {\n          &::before {\n            background-image: url('../images/icon-twitter-black.svg');\n          }\n        }\n      }\n\n      &.icon-youtube a {\n        &:hover, &:focus {\n          &::before {\n            background-image: url('../images/icon-youtube-black.svg');\n          }\n        }\n      }\n    }\n  }\n}\n",".line {\n  position: absolute;\n  transition: all 0.5s;\n  z-index: 1;\n\n  &.purple-light {\n    background: lighten($color-purple, 20%);\n  }\n\n  &.green-light {\n    background: $color-green;\n  }\n\n  &.teal-light {\n    background: lighten($color-teal, 20%);\n  }\n\n  html[data-contrast=\"true\"] & {\n    background: $color-white;\n  }\n\n  &.vertical {\n    width: 2px;\n    height: 0;\n    top: 0;\n\n    &.bottom {\n      bottom: 0;\n      top: auto;\n    }\n  }\n\n  &.horizontal {\n